.return-ctnr {
  position: fixed;
  bottom: 0;
  right: 3vw;
  height: 40px;
  width: 90px;
  background-color: white;
  border-radius: 20px 20px 0 0;
  opacity: 1;

  &__top {
    color: blue;
    font-size: 0.8rem;
  }
}

/* Animation styling */
.slide-in {
  opacity: 1;
  transform: translateY(0);
}

.slide-out {
  opacity: 0;
  transform: translateY(10vh);
}

.slide-in, .slide-out {
  transition:
    opacity 1s,
    transform 1s;
}