* {
  scroll-behavior: smooth;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-margin-top: 20px;
  font-family: Arial, Helvetica, sans-serif;
}

div, button {
  justify-content: center;
  align-items: center;
  display: flex;
}

p, li, code {
  font-size: 1rem;
  line-height: 1.5rem;
}

a {
  cursor: pointer;
  color: #f0f0f0;
}

code {
  overflow-wrap: break-word;
  margin: 30px 0 30px 60px;
  font-family: monospace;
  font-size: 1rem;
  display: block;
}

.nav-ctnr__header, .main-doc__unit-header {
  font-size: 1.8rem;
}

.nav-ctnr {
  display: unset;
  width: 320px;
  height: 100%;
  z-index: 10;
  background-color: #282828;
  transition: all 1s;
  position: fixed;
  top: 0;
  left: 0;
}

.nav-ctnr__navbar, .nav-ctnr__slider-tab {
  position: absolute;
}

.nav-ctnr__navbar {
  height: 100%;
  width: 95%;
  z-index: 10;
  border-right: 2px solid gray;
}

.nav-ctnr__header {
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 20px;
  transition: all 1s;
  display: flex;
}

.nav-ctnr__header img {
  height: 40px;
  width: 40px;
}

.nav-ctnr__nav-group {
  display: unset;
}

.nav-ctnr__link {
  border-top: 1px solid gray;
  padding: 12px 20px;
  font-size: 1.1rem;
  text-decoration: none;
  display: block;
}

.nav-ctnr__link:last-child {
  border-bottom: 1px solid gray;
}

.nav-ctnr__link:hover {
  color: #fff;
  background-color: #007fff;
  transition: background-color 50ms;
}

.nav-ctnr__slider-tab {
  width: 25px;
  height: 100px;
  cursor: pointer;
  z-index: 9;
  border: 2px solid gray;
  border-radius: 0 15px 15px 0;
  flex-direction: column;
  top: 10%;
  left: calc(95% - 2px);
}

.nav-ctnr__slider-tab #tab-arrow {
  font-size: .7rem;
}

.return-ctnr {
  height: 40px;
  width: 90px;
  opacity: 1;
  background-color: #fff;
  border-radius: 20px 20px 0 0;
  position: fixed;
  bottom: 0;
  right: 3vw;
}

.return-ctnr__top {
  color: #00f;
  font-size: .8rem;
}

.slide-in {
  opacity: 1;
  transform: translateY(0);
}

.slide-out {
  opacity: 0;
  transform: translateY(10vh);
}

.slide-in, .slide-out {
  transition: opacity 1s, transform 1s;
}

html {
  color: #f0f0f0;
  background-color: #282828;
}

.main-doc, .main-doc__section {
  flex-direction: column;
  display: flex;
}

.main-doc {
  height: 100%;
  gap: 20px;
  padding: 0 30px;
  transition: all 1s;
  position: fixed;
  top: 0;
  left: 350px;
  overflow-y: scroll;
}

.main-doc__section {
  gap: 20px;
}

.main-doc__section:last-child {
  margin-bottom: 60px;
}

.main-doc__section ul {
  margin-left: 40px;
}

.main-doc__section ul li, .main-doc__section ul li ul {
  margin-left: 20px;
}

@media (max-width: 1000px) {
  .nav-ctnr {
    height: 250px;
    width: 100%;
    border-bottom: 1px solid gray;
  }

  .nav-ctnr__navbar {
    width: 100%;
    border-right: none;
  }

  .nav-ctnr__header {
    z-index: 1;
    width: 100%;
    border-bottom: 1px solid gray;
    position: fixed;
    top: 0;
  }

  .nav-ctnr__nav-group {
    width: 100%;
    height: calc(100% - 80px);
    z-index: -1;
    position: absolute;
    top: 80px;
    overflow-y: scroll;
  }

  .nav-ctnr__link:last-child {
    border-bottom: none;
  }

  .nav-ctnr__slider-tab {
    visibility: hidden;
  }

  .main-doc {
    height: calc(100% - 250px);
    width: 100%;
    top: 250px;
    left: 0 !important;
  }

  .return-ctnr {
    height: 30px;
  }
}

